<!--
 * @Description: 以上评价评分
 * @Date: 2019-09-29 10:02:18
 * @LastEditors: Ji Yinghao
 * @LastEditTime: 2019-10-09 18:09:44
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items_" />

    <v-card class="mx-auto">
      <v-avatar size="36px">
        <img v-if="img !== ''" :src="img" alt="Avatar" />
        <v-icon v-else>tag_faces</v-icon>
      </v-avatar>
      <span style="margin-left: 15px;font-size: 16px;">{{ name }}</span>
    </v-card>

    <v-card class="mx-auto">
      <v-list two-line v-if="items.length > 0">
        <template v-for="(item, index) in items">
          <v-list-tile :key="index" avatar ripple @click="particular( item )">
            <v-list-tile-content>
              <v-list-tile-title
                style="line-height: 25px;height: 25px;"
              >{{ item.user_name }} ( {{ item.user_phone }} )</v-list-tile-title>
              <v-list-tile-sub-title class="text--primary">{{ item.content }}</v-list-tile-sub-title>
              <v-list-tile-sub-title>{{ item.create_time | time }}</v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-list-tile-action-text>{{ item.score/20 }} 分</v-list-tile-action-text>
              <span v-if="item.score == 20">
                <v-icon color="grey lighten-1">star_border</v-icon>
              </span>
              <span v-if="item.score == 40">
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
              </span>
              <span v-if="item.score == 60">
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
              </span>
              <span v-if="item.score == 80">
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
              </span>
              <span v-if="item.score == 100">
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
                <v-icon color="grey lighten-1">star_border</v-icon>
              </span>
            </v-list-tile-action>
          </v-list-tile>
          <v-divider v-if="index + 1 < items.length" :key="`divider-${index}`"></v-divider>
        </template>
      </v-list>
    </v-card>
    <v-card style="padding-right: 30px;" >
      <div v-if="items.length > 0" class="text-xs-center" style="width: 100%;margin: 10px;">
        <Page @opinionPage="info_" :total_number="total_number" :page="page+1" />
      </div>
    </v-card>
    <v-card class="mx-auto" v-if="items.length == 0">
      <p class="p_text">没有更多数据了 :(</p>
    </v-card>
    <!-- 详情弹框 -->
    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >{{ particular_data.user_name }} 的评分/评论</v-card-title>

        <v-card-text>
          <p>
            <span class="particular_data">用户昵称</span>
            {{ particular_data.user_name }}
          </p>

          <p>
            <span class="particular_data">用户账号</span>
            {{ particular_data.user_phone }}
          </p>

          <p>
            <span class="particular_data">用户ID</span>
            {{ particular_data.user_id }}
          </p>

          <p>
            <span class="particular_data">评论内容</span>
            {{ particular_data.content }}
          </p>

          <p>
            <span class="particular_data">评分</span>
            {{ particular_data.score/20 }} 星
          </p>

          <p>
            <span class="particular_data"></span>
            <span v-if="particular_data.score == 20">
              <v-icon color="grey lighten-1">star_border</v-icon>
            </span>
            <span v-if="particular_data.score == 40">
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
            </span>
            <span v-if="particular_data.score == 60">
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
            </span>
            <span v-if="particular_data.score == 80">
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
            </span>
            <span v-if="particular_data.score == 100">
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
              <v-icon color="grey lighten-1">star_border</v-icon>
            </span>
          </p>

          <p>
            <span class="particular_data">评论时间</span>
            {{ particular_data.create_time | time }}
          </p>

          <p>
            <span class="particular_data">操作</span>
            <v-btn color="error" @click="del_box( particular_data.id )">删除此评分</v-btn>
          </p>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="dialog = false">确定</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 确认弹框 -->
    <v-dialog v-model="dialog_" max-width="500">
      <v-card>
        <v-card-title class="headline">确定要删除此条用户评分/评论吗?</v-card-title>
        <v-card-text>谨慎操作，删除之后将不可恢复。</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_ = false;dialog = true ">取消</v-btn>
          <v-btn color="red darken-1" flat="flat" @click="del">确认</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Page from "../../opinion/page";
export default {
  components: {
    Breadcrumbs,
    Page
  },
  name: "doctorRating",
  data() {
    return {
      // 分页数据
      page: 0,
      total_number: 0,
      // 面包屑
      items_: [
        { text: "首页", disabled: false, to: "/home" },
        { text: "医生评分/评价", disabled: false, to: "/doctorRating" }
      ],
      items: [],
      // 详情弹框
      dialog: false,
      particular_data: {
        content: "",
        create_time: 0,
        doctor_id: 0,
        id: 0,
        score: 20,
        user_id: 0,
        user_name: "",
        user_phone: ""
      },
      // 确认框
      dialog_: false,
      id: 0,
      name: "",
      img: ""
    };
  },
  mounted() {
    this.name = this.$route.query.name;
    this.img = this.$route.query.img;
    this.info();
  },
  methods: {
    // 删除确认框
    del_box(id) {
      this.dialog = false;
      this.dialog_ = true;
      this.id = id;
    },
    // 删除操作
    del() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.website_score_del,
        data: {
          id: this.id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({
              text: "用户评论已删除！",
              timeout: 3000,
              snackbar: true
            });
            this.dialog_ = false;
            this.info();
          } else {
            this.$Dialog.getProductCheckResult_({
              text: response.data.message,
              timeout: 3000,
              snackbar: true
            });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
            text: error,
            timeout: 3000,
            snackbar: true
          });
        });
    },
    //  详情弹框
    particular(item) {
      this.particular_data = item;
      this.dialog = true;
    },
    // 分页
    info_(e) {
      this.page = e - 1;
      this.info();
    },
    // 列表
    info() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.website_score_list,
        data: {
          doctor_id: this.$route.query.id - 0,
          page: this.page
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.items = response.data.datas.list;
            this.total_number = response.data.datas.total_number;
            if( this.items.length == 0 ){
              window.history.go(-1);
              this.$Dialog.getProductCheckResult_({
              text: '该医生没有评价记录！',
              timeout: 3000,
              snackbar: true
            });
            }
          } else {
            this.$Dialog.getProductCheckResult_({
              text: response.data.message,
              timeout: 3000,
              snackbar: true
            });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
            text: error,
            timeout: 3000,
            snackbar: true
          });
        });
    }
  }
};
</script>

<style scoped>
.particular_data {
  display: inline-block;
  width: 100px;
}
.v-card__text {
  padding: 23px;
  width: 100%;
}
.text-xs-center > div {
  text-align: right;
  margin-right: 30px;
}
hr {
  background-color: #f7f7f7;
}
.theme--light.application .text--primary {
  color: #1a237e !important;
  font-size: 16px;
}
.mx-auto {
  padding: 10px 15px;
  margin-bottom: 10px;
}
.mx-auto > p {
  margin-bottom: 0;
}
</style>